import MyTable from "./MyTable";
import { useState } from "react";
import { Link } from "react-router-dom";
import DataContext from "../context/DataContext";
import React from "react";
import DataFilterContext from "../context/DataFilterContext";
import ClayButton, {ClayButtonWithIcon} from '@clayui/button';
import {ClayDropDownWithItems} from '@clayui/drop-down';
import {ClayCheckbox, ClayInput} from '@clayui/form';
import ClayIcon, {ClayIconSpriteContext} from '@clayui/icon';
import ClayLabel from '@clayui/label';
import ClayManagementToolbar, {
	ClayResultsBar,
} from '@clayui/management-toolbar';
import spritemap from "./icons.svg";


function TabOne(props) {
  // const { data } = props;
  const [search, setsearch] = useState("");
  const data = React.useContext(DataContext);
  const [mydata, setMyData] = useState(data);
  const [clicksearch,setClickSearch]=useState(false)
  const onchange = (e) => {
    setsearch(e.target.value);
    let mysearch = [...data];
    if (e.target.value === "") {
      setMyData(mysearch);
    } else {
      setMyData(
        mysearch.filter((item) => {
          return item.name.toLowerCase().indexOf(search.toLowerCase()) !== -1;
        })
      );
    }
  };
  const handleFilter = (e) => {
    let search = [...data];
    if (e === "2") {
      setMyData(search.filter((item) => item.type === 1));
    }
    if (e === "3") {
      setMyData(search.filter((item) => item.type === 2));
    }
    if (e === "1") {
      setMyData(data);
    }
  };
  const passItem = (item) => {
    props.passItem(item);
  };
  
  const filterItems = [
    { label: "همه",value: "1", onClick: () => handleFilter("1")},
    { label: "سیما",value: "2", onClick: () => handleFilter("2")},
    { label: "صدا",value: "3", onClick: () => handleFilter("3")}
  ];

  const viewTypes = [
    {
      label: "List",
      onClick: () => alert("Show view list"),
      symbolLeft: "list"
    },
    {
      active: true,
      label: "Table",
      onClick: () => alert("Show view table"),
      symbolLeft: "table"
    },
    {
      label: "Card",
      onClick: () => alert("Show view card"),
      symbolLeft: "cards2"
    }
  ];
  const onSearch=()=>{
    setClickSearch(true)
  }
  const onClear = ()=>{
    setClickSearch(false)
  }
  const [searchMobile, setSearchMobile] = useState(false);

  const viewTypeActive = viewTypes.find(type => type.active);


  return (
    <div>
      <DataFilterContext.Provider value={mydata}>
        <div className="ml-5 mr-5 mt-2">
        <ClayManagementToolbar>
          <ClayManagementToolbar.ItemList>
            <ClayManagementToolbar.Item>
              <ClayCheckbox checked={false} onChange={() => {}} />
            </ClayManagementToolbar.Item>
  
            <ClayDropDownWithItems
              items={filterItems}
              spritemap={spritemap}
              trigger={
                <ClayButton className="nav-link" displayType="unstyled">
                  <span className="navbar-breakpoint-down-d-none">
                    <span className="navbar-text-truncate">
                      {"Filter and Order"}
                    </span>
                    <ClayIconSpriteContext.Provider value={spritemap}>
                    <ClayIcon
                      className="inline-item inline-item-after"
                      symbol="caret-bottom"
                    /></ClayIconSpriteContext.Provider>
                  </span>
                  <span className="navbar-breakpoint-d-none">
                    <ClayIcon spritemap={spritemap} symbol="filter" />
                  </span>
                </ClayButton>
              }
            />
  
            <ClayManagementToolbar.Item>
              <ClayButton
                className="nav-link nav-link-monospaced"
                displayType="unstyled"
                onClick={() => {}}
              >
                <ClayIcon spritemap={spritemap} symbol="order-list-up" />
              </ClayButton>
            </ClayManagementToolbar.Item>
          </ClayManagementToolbar.ItemList>
  
          <ClayManagementToolbar.Search showMobile={searchMobile}>
            <ClayInput.Group>
              <ClayInput.GroupItem>
                <ClayInput
                  aria-label="Search"
                  className="form-control input-group-inset input-group-inset-after"
                  type="text"
                  onChange={(e)=>{onchange(e)}}
                />
                <ClayInput.GroupInsetItem after  onClick={(e)=>onSearch(e)} tag="span">
                  <ClayButtonWithIcon
                    className="navbar-breakpoint-d-none"
                    displayType="unstyled"
                    onClick={() => setSearchMobile(false)}
                    spritemap={spritemap}
                    symbol="times"
                  />
                  <ClayButtonWithIcon
                    displayType="unstyled"
                    spritemap={spritemap}
                    symbol="search"
                   
                  />
                </ClayInput.GroupInsetItem>
              </ClayInput.GroupItem>
            </ClayInput.Group>
          </ClayManagementToolbar.Search>
  
          <ClayManagementToolbar.ItemList>
            <ClayManagementToolbar.Item className="navbar-breakpoint-d-none">
              <ClayButton
                className="nav-link nav-link-monospaced"
                displayType="unstyled"
                onClick={() => setSearchMobile(true)}
              >
                <ClayIcon spritemap={spritemap} symbol="search" />
              </ClayButton>
            </ClayManagementToolbar.Item>
  
            <ClayManagementToolbar.Item>
              <ClayButton
                className="nav-link nav-link-monospaced"
                displayType="unstyled"
                onClick={() => {}}
              >
                <ClayIcon spritemap={spritemap} symbol="info-circle-open" />
              </ClayButton>
            </ClayManagementToolbar.Item>
  
            <ClayManagementToolbar.Item>
              <ClayDropDownWithItems
                items={viewTypes}
                spritemap={spritemap}
                trigger={
                  <ClayButton
                    className="nav-link-monospaced nav-link"
                    displayType="unstyled"
                  >
                    <ClayIcon
                      spritemap={spritemap}
                      symbol={viewTypeActive ? viewTypeActive.symbolLeft : ""}
                    />
                  </ClayButton>
                }
              />
            </ClayManagementToolbar.Item>
  
            <ClayManagementToolbar.Item>
            <Link to="/add">
              <ClayButtonWithIcon
                className="nav-btn nav-btn-monospaced"
                spritemap={spritemap}
                symbol="plus"
              />
              </Link>
            </ClayManagementToolbar.Item>
          </ClayManagementToolbar.ItemList>
        </ClayManagementToolbar>
                {clicksearch &&
        <ClayResultsBar>
          <ClayResultsBar.Item>
            <span className="component-text text-truncate-inline">
              <span className="text-truncate">
                {'2 results for "'}
                <strong>{search}</strong>
                {'"'}
              </span>
            </span>
          </ClayResultsBar.Item>
          <ClayResultsBar.Item expand>
            <ClayLabel
              className="component-label tbar-label"
              displayType="unstyled"
              spritemap={spritemap}
            >
              {"Filter"}
            </ClayLabel>
          </ClayResultsBar.Item>
          <ClayResultsBar.Item>
            <ClayButton
              className="component-link tbar-link"
              displayType="unstyled"
              onClick={onClear}
            >
              {"Clear"}
            </ClayButton>
          </ClayResultsBar.Item>
        </ClayResultsBar>}
          {/* <ManagementToolbar/> */}

          <div>
            <MyTable passItem={passItem} />
          </div>
        </div>
      </DataFilterContext.Provider>
    </div>
  );
}

export default TabOne;
